import {mapState, mapMutations, mapActions} from 'vuex'

export default {
    data() {
        return {
            isConvenient: false
        }
    },
    computed: {
        ...mapState([
            'projectName',
            'systemName',
            'globalLoading',
            'user',
        ]),
        username() {
            return this.user ? (this.user.merchantName || this.user.account) : '';
        }
    },
    methods: {
        logout() {
            const that = this

            that.$vux.confirm.show({
                title: '提示',
                content: '确认退出吗?',
                onCancel () {},
                onConfirm () {
                    api.post('/logout').then(() => {
                        that.$vux.toast.show({
                            text: '退出成功',
                            type: 'success'
                        })
                    })
                    store.dispatch('clearUserInfo');
                    that.$router.replace('/login')
                }
            })
        },
    },
    created() {
        if(!this.user) {
            this.$vux.toast.show({
                text: '您尚未登录',
                type: 'warn'
            })
            router.replace('/login')
            return 
        }
        
        const path = this.$router.history.current.path
        
        if(path == '/') {
            //跳转到个人资料页
            this.$router.replace('/userinfo');
        }
    },
    components: {},
    watch: {}
}



